import {
    mapState,
    mapMutations
} from 'vuex';
import store from "../../store/index.js";
import api from "../../api/api.js";
export default {
    computed: mapState(['hasLogin', 'userInfo', 'token']),
    data() {
        return {}
    },
    onPullDownRefresh: function() {
        if (this.hasLogin) {
            this.reFresh();
        } else {
            uni.stopPullDownRefresh();
            return;
        }
        setTimeout(() => {
            uni.hideLoading()
            uni.stopPullDownRefresh()
        }, 1000)
    },
    methods: {
        bindLogin() {
            if (this.hasLogin) {
                this.logout()
            } else {
                uni.navigateTo({
                    url: 'login/login'
                })
            }
        },
        goLogin() {
            if (!this.hasLogin) {
                uni.navigateTo({
                    url: 'login/login'
                })
            }
        },
        goSetting() {
            uni.navigateTo({
                url: 'setting/setting'
            })
        },
        goMessage() {
            if (this.hasLogin) {
                uni.navigateTo({
                    url: '../../pages/messages/messages'
                })
            }
        },
        reFresh() {
            var url = api.urls.getSelfUserInfo;
            var data = {};
            api.req.get(url, data, (res) => {
                if (res.code === 0) {
                    var urlHead = api.urls.getHead;
                    var dataHead = {};
                    let userInfoGet = res.data;
                    api.req.get(urlHead, dataHead, (resHead) => {
                        if (resHead.code === 0) {
                            userInfoGet.headUrl = 'http://' + resHead.data;
                            console.log(userInfoGet.headUrl);
                            delete userInfoGet.password;
                            store.commit("saveUserInfo", userInfoGet);
                        } else {
                            userInfoGet.headUrl = '/static/icons/logo.png';
                        }
                    });
                    console.log(this.userInfo);
                    uni.stopPullDownRefresh();
                } else {
                    uni.showModal({
                        content: res.msg,
                        showCancel: false
                    })
                }
            });
        },
        goFollow() {
            if (this.hasLogin) {
                uni.navigateTo({
                    url: 'my-fallows/my-fallows' //关注界面路径
                });
            }
        },
        goCollect() {
            if (this.hasLogin) {
                uni.navigateTo({
                    url: 'my-collects/my-collects' //收藏界面路径
                });
            }
        },
        goPost() {
            if (this.hasLogin) {
                uni.navigateTo({
                    url: 'my-post/my-post' //我的帖子界面路径
                });
            }
        },
        goComment() {
            if (this.hasLogin) {
                uni.navigateTo({
                    url: 'my-comments/my-comments' //我的评论界面路径
                });
            }
        },
        goEditInformation() {
            if (this.hasLogin) {
                uni.navigateTo({
                    url: '../../pages/my/setting/change-userInfo/change-userInfo' //修改用户资料路径
                });
            }
        },
        ...mapMutations(['login'])
    }
}
